import React, { Component } from 'react'
import Link from 'next/link'
import styled from 'styled-components'
import uuid from "uuid/v4";

 const Outerbox = styled.div`
  background: green;
  width: 80%;
  margin-left: 50%;
  transform: translateX(-50%);
 `;
  const NavItem = styled.div`
  `;
 const Title = styled.div`
  height: 40px;
  font-size: 17px;
  line-height: 20px;
  border-radius: 10px 10px 0px 0px;
  input:active, input:focus{
    box-shadow: ${props => props.theme.bsblack};
}
 `;
 
class links extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navItems: [
        { id: uuid(), name: "Edit Profile",},
        { id: uuid(), name: "Resource Uploads" },
        { id: uuid(), name: "Impact Reporting" },
        { id: uuid(), name: "Theme Customization" },
        { id: uuid(), name: "Account Plan" },
        { id: uuid(), name: "Verification" }
      ]
    }
  }
  renderNavItems() {
    return(
      <div>
        {this.state.navItems.map(item => (
          <Link key={item.id} href={`/${item.link}`}>
            <a>
              <Title>{item.name}</Title>
            </a>
          </Link>
        ))}
      </div>
    )
  }
  render() {
    return (
      <Outerbox>
        {this.renderNavItems()}
      </Outerbox>
     )
   }
 }

export default links
